import React from 'react';

function Footer({ language, translations }) {
  // 
  const getText = (key) => {
    return (translations[language] && translations[language][key]) || translations.en[key];
  };

  // 
  const getLocalizedText = () => {
    switch (language) {
      case 'zh':
        return {
          copyright: " 2024 . .",
          privacy: "",
          terms: "",
          contact_us: "",
          description: ""
        };
      case 'ja':
        return {
          copyright: " 2024 . .",
          privacy: "",
          terms: "",
          contact_us: "",
          description: ""
        };
      case 'ko':
        return {
          copyright: " 2024   .   .",
          privacy: " ",
          terms: "",
          contact_us: "",
          description: "        ."
        };
      case 'es':
        return {
          copyright: " 2024 Extractor de Fotogramas de Vdeo. Todos los derechos reservados.",
          privacy: "Poltica de Privacidad",
          terms: "Trminos de Uso",
          contact_us: "Contacto",
          description: "Extractor de Fotogramas es una herramienta en lnea para extraer fotogramas de vdeos."
        };
      case 'fr':
        return {
          copyright: " 2024 Extracteur de Frames Vido. Tous droits rservs.",
          privacy: "Politique de Confidentialit",
          terms: "Conditions d'Utilisation",
          contact_us: "Contactez-nous",
          description: "L'Extracteur de Frames Vido est un outil en ligne pour extraire des images de vidos."
        };
      case 'de':
        return {
          copyright: " 2024 Video-Frame-Extraktor. Alle Rechte vorbehalten.",
          privacy: "Datenschutzrichtlinie",
          terms: "Nutzungsbedingungen",
          contact_us: "Kontakt",
          description: "Video-Frame-Extraktor ist ein Online-Tool zum Extrahieren von Frames aus Videos."
        };
      default: // English
        return {
          copyright: " 2024 Video Frame Extractor. All rights reserved.",
          privacy: "Privacy Policy",
          terms: "Terms of Use",
          contact_us: "Contact Us",
          description: "Video Frame Extractor is an online tool for extracting frames from videos."
        };
    }
  };

  const localText = getLocalizedText();

  return (
    <footer className="bg-gray-800 text-white py-6">
      <div className="container mx-auto px-4">
        <div className="flex flex-col md:flex-row justify-between items-center">
          <div className="mb-4 md:mb-0">
            <p>{localText.copyright}</p>
          </div>
          <div>
            <ul className="flex space-x-4">
              <li><a href="#privacy" className="hover:text-blue-300">{localText.privacy}</a></li>
              <li><a href="#terms" className="hover:text-blue-300">{localText.terms}</a></li>
              <li><a href="#contact" className="hover:text-blue-300">{localText.contact_us}</a></li>
            </ul>
          </div>
        </div>
        <div className="mt-4 text-center text-sm text-gray-400">
          <p>{localText.description}</p>
          <p>{getText('contact')}</p>
        </div>
      </div>
    </footer>
  );
}

export default Footer; 