//  - 
console.log(':', {
  REACT_APP_API_URL: process.env.REACT_APP_API_URL,
  API_URL,
  STORAGE_URL
});

function App() {
  const [videoFile, setVideoFile] = useState(null);
  const [frames, setFrames] = useState([]);
  const [currentStep, setCurrentStep] = useState(0);
  const [isExtracting, setIsExtracting] = useState(false);
  const [progress, setProgress] = useState(0);
  const [error, setError] = useState(null);
  
  //  'en' 
  const [language, setLanguage] = useState(() => {
    const savedLanguage = localStorage.getItem('language');
    return savedLanguage || 'en';
  });
  
  const [showLanguageMenu, setShowLanguageMenu] = useState(false);

  // 
  useEffect(() => {
    if (!localStorage.getItem('language')) {
      localStorage.setItem('language', 'en');
    }
  }, []);

  // 
  const getText = (key) => {
    return (TRANSLATIONS[language] && TRANSLATIONS[language][key]) || TRANSLATIONS.en[key];
  };

  // 
  const handleVideoUpload = (file) => {
    setVideoFile(file);
    setCurrentStep(1);
  };
  
  // 
  const handleExtractFrames = async (options) => {
    if (!videoFile) {
      setError(getText('noVideoUploaded'));
      return;
    }
    
    setIsExtracting(true);
    setProgress(0);
    setError(null);
    
    try {
      // FormData
      const formData = new FormData();
      formData.append('video', videoFile);
      formData.append('fps', options.fps || 1);
      
      console.log(':', videoFile);
      console.log('FPS:', options.fps || 1);
      console.log('API:', API_URL);
      
      // API - 
      const response = await fetch(`${API_URL}/api/extract-frames`, {
        method: 'POST',
        body: formData
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || getText('extractionFailed'));
      }
      
      const result = await response.json();
      console.log(':', result);
      
      // URL
      if (result.frames && result.frames.length > 0) {
        result.frames = result.frames.map(frame => {
          if (frame.url && !frame.url.includes('storage.y.cheesecatool.com')) {
            // URLstorage URL
            const pathParts = frame.url.split('/frames/');
            if (pathParts.length > 1) {
              frame.url = `${STORAGE_URL}/frames/${pathParts[1]}`;
            }
          }
          return frame;
        });
      }
      
      // 
      setFrames(result.frames || []);
      setCurrentStep(2);
    } catch (error) {
      console.error(':', error);
      setError(error.message);
    } finally {
      setIsExtracting(false);
    }
  };

  // 
  const toggleLanguageMenu = () => {
    setShowLanguageMenu(!showLanguageMenu);
  };

  // 
  const selectLanguage = (langCode) => {
    setLanguage(langCode);
    setShowLanguageMenu(false);
    // localStorage
    localStorage.setItem('language', langCode);
    console.log(`: ${langCode}`);
  };

  // 
  const getCurrentLanguage = () => {
    return LANGUAGES.find(lang => lang.code === language) || LANGUAGES[0];
  };

  return (
    <div className="App">
      {/*  */}
      <div className="top-nav">
        <img src="images/cat-icon.png" alt="" className="nav-logo" />
        <div className="nav-title">{getText('navTitle')}</div>
        <div className="nav-links">
          <a href="https://y.cheesecatool.com/" className="nav-link" target="_blank" rel="noopener noreferrer">{getText('navLinkUpload')}</a>
          <a href="https://y.cheesecatool.com/url" className="nav-link" target="_blank" rel="noopener noreferrer">{getText('navLinkUrl')}</a>
          <a href="https://yt.cheesecatool.com/" className="nav-link" target="_blank" rel="noopener noreferrer">{getText('navLinkYoutube')}</a>
        </div>

        {/*  */}
        <div className="language-selector">
          <button 
            className="language-btn" 
            onClick={toggleLanguageMenu}
            aria-label="Select language"
          >
            <span className="language-flag">{getCurrentLanguage().flag}</span>
            <span className="language-name">{getCurrentLanguage().name}</span>
            <span className="dropdown-arrow"></span>
          </button>
          
          {showLanguageMenu && (
            <div className="language-dropdown">
              {LANGUAGES.map(lang => (
                <button 
                  key={lang.code} 
                  className={`language-option ${lang.code === language ? 'active' : ''}`}
                  onClick={() => selectLanguage(lang.code)}
                >
                  <span className="language-flag">{lang.flag}</span>
                  <span className="language-name">{lang.name}</span>
                </button>
              ))}
            </div>
          )}
        </div>
      </div>
      
      <div className="container">
        <div className="app-content">
          <div className="title-section">
            <img src="/logo.png" alt={getText('logoAlt')} className="logo-icon" />
            <h1>{getText('pageTitle')}</h1>
          </div>
          
          {error && (
            <div className="error-message">
              {error}
            </div>
          )}
          
          {/* 1:  */}
          {currentStep === 0 && (
            <UploadForm 
              onVideoUpload={handleVideoUpload} 
              getText={getText}
            />
          )}
          
          {/* 2:  */}
          {currentStep === 1 && videoFile && (
            <VideoUpload 
              videoFile={videoFile} 
              onExtract={handleExtractFrames}
              language={language}
              translations={translations}
            />
          )}
          
          {/* 3:  */}
          {currentStep === 2 && frames.length > 0 && (
            <FrameGallery 
              frames={frames} 
              getText={getText}
            />
          )}
          
          {isExtracting && (
            <div className="loading-container">
              <div className="spinner"></div>
              <p>{getText('processing')}</p>
              {progress > 0 && (
                <div className="progress-bar">
                  <div 
                    className="progress-fill" 
                    style={{ width: `${progress}%` }}
                  ></div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
      
      <Footer language={language} translations={TRANSLATIONS} />
    </div>
  );
}

export default App; 